body {
  background: #f5f5f5;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 15px;
  margin-top: 20px;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 5px #0000001a;
}

.photo-card img {
  width: 100%;
  display: block;
}

.stats {
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.stats-item {
  align-items: center;
  font-size: 14px;
  display: flex;
}

.material-icons {
  margin-right: 5px;
}

#load-more {
  cursor: pointer;
  margin: 30px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}
